<h3 id="shu-xing-3"><a class="anchor" href="#shu-xing-3" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 属性</h3>
<p><prop-table :data="tableData6"></prop-table></p>
<h2 id="dong-hua"><a class="anchor" href="#dong-hua" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 动画</h2>
<h3 id="jian-dan-dong-hua"><a class="anchor" href="#jian-dan-dong-hua" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 简单动画</h3>
<h4 id="set-yu-fa"><a class="anchor" href="#set-yu-fa" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> SET语法</h4>
<pre v-pre=""><code v-pre="">SetExpr
    : SetExpr then SetExpr
    | { SetExpr+ }
    | set id {
        (id = (value_expr | [value_expr, string]))*
    } time_expr (, string)?
</code></pre>
<h4 id="shi-li-4"><a class="anchor" href="#shi-li-4" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 示例</h4>
<code-block :code="{&quot;code&quot;:&quot;def text t {\n  content = \&quot;bilibili\&quot;\n}\nset t {\n    x = 50%\n    y = [50%, \&quot;easeOut\&quot;]\n} 1s, \&quot;linear\&quot;\n&quot;}"><pre v-pre=""><code class="hljs language-js">def text t {
  content = <span class="hljs-string">"bilibili"</span>
}
set t {
    x = <span class="hljs-number">50</span>%
    y = [<span class="hljs-number">50</span>%, <span class="hljs-string">"easeOut"</span>]
} <span class="hljs-number">1</span>s, <span class="hljs-string">"linear"</span>
</code></pre>
</code-block>
<h4 id="shuo-ming"><a class="anchor" href="#shuo-ming" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 说明</h4>
<pre v-pre=""><code class="hljs language-javascript">set 目标对象 {
    可渐变属性 = 目标值
    可渐变属性 = [目标值, <span class="hljs-string">"插值类型"</span>]
} 动画持续, <span class="hljs-string">"区块默认的插值类型"</span>
</code></pre>
<p>set 语句块定义了一个对象的一组属性在相同时间内的动画。</p>
<h5 id="shu-xing-4"><a class="anchor" href="#shu-xing-4" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 属性</h5>
<p>只有目标对象的可渐变属性才有正常的动画效果。对可变属性但是非渐变属性设置新值会立即生效。对其他属性设值或者不存在的属性设值将被忽略。</p>
<p>原则上某一属性在一个 set 语句中最多只能出现一次。在实现上，如果多次出现，以最后一次为准。</p>
<h5 id="mu-biao-zhi"><a class="anchor" href="#mu-biao-zhi" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 目标值</h5>
<p>目标值应该与属性所需的值为同一类型。可渐变属性的可以用一个数组的形式，特别指定该属性的插件类型。</p>
<h5 id="cha-zhi-lei-xing"><a class="anchor" href="#cha-zhi-lei-xing" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 插值类型</h5>
<p>区块默认的插值类型写在区块动画时间之后，与区块动画时间用逗号隔开。区块默认的插值类型可以省略。在省略时，默认的插值类型为 &quot;linear&quot;。可渐变属性的可以用一个数组的形式，特别指定该属性的插件类型。</p>
<h3 id="chuan-lian-dong-hua"><a class="anchor" href="#chuan-lian-dong-hua" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 串联动画</h3>
<h4 id="shi-li-5"><a class="anchor" href="#shi-li-5" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 示例</h4>
<code-block :code="{&quot;code&quot;:&quot;def text a {\n  content = \&quot;bilibili\&quot;\n}\nset a {\n    x = 50%\n    y = 50%\n} 1s\nthen set a {\n    alpha = 0\n} 1s\n&quot;}"><pre v-pre=""><code class="hljs language-js">def text a {
  content = <span class="hljs-string">"bilibili"</span>
}
set a {
    x = <span class="hljs-number">50</span>%
    y = <span class="hljs-number">50</span>%
} <span class="hljs-number">1</span>s
then set a {
    alpha = <span class="hljs-number">0</span>
} <span class="hljs-number">1</span>s
</code></pre>
</code-block>
<h4 id="shuo-ming-2"><a class="anchor" href="#shuo-ming-2" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 说明</h4>
<pre v-pre=""><code class="hljs language-javascript">set语句(组) then set语句(组) then set语句(组) ...
</code></pre>
<p>后一语句动画在前语句动画完毕后开始。</p>
<h3 id="bing-lian-dong-hua"><a class="anchor" href="#bing-lian-dong-hua" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 并联动画</h3>
<code-block :code="{&quot;code&quot;:&quot;def text a {\n  content = \&quot;bilibili\&quot;\n}\nset a {\n    x = 50%\n    y = 50%\n} 1s\nset a {\n    alpha = 0\n} 1s\n&quot;}"><pre v-pre=""><code class="hljs language-js">def text a {
  content = <span class="hljs-string">"bilibili"</span>
}
set a {
    x = <span class="hljs-number">50</span>%
    y = <span class="hljs-number">50</span>%
} <span class="hljs-number">1</span>s
set a {
    alpha = <span class="hljs-number">0</span>
} <span class="hljs-number">1</span>s
</code></pre>
</code-block>
<h4 id="shuo-ming-3"><a class="anchor" href="#shuo-ming-3" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 说明</h4>
<pre v-pre=""><code v-pre="">set语句
set语句
...
</code></pre>
<p>没有then连接的连续出现的set语句定义的动画并行执行。</p>
<h5 id="zong-shi-jian"><a class="anchor" href="#zong-shi-jian" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 总时间</h5>
<p>并行语句的总时间是子语句的最长时间。一般情况下子语句的动画时间是相同的。如果子语句动画提前执行完，则等待总其他并行动画。</p>
<h5 id="tong-yi-mu-biao-shu-xing-tong-shi-geng-gai"><a class="anchor" href="#tong-yi-mu-biao-shu-xing-tong-shi-geng-gai" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 同一目标属性同时更改</h5>
<p>在并行语句中出现同一目标属性在多个子语句中有更改，最后更改为准。之前的属性动画忽略。</p>
<h5 id="yu-ju-zu"><a class="anchor" href="#yu-ju-zu" aria-hidden="true"><svg aria-hidden="true" class="octicon octicon-link" height="16" version="1.1" viewBox="0 0 16 16" width="16"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg></a> 语句组</h5>
<pre v-pre=""><code v-pre="">{
    set语句
    set语句
    ...
}
</code></pre>
<p>并行语句用大括号包围后，成为一个set语句单位。</p>
</section></template>

<script>
  export default {
    data() {
      return {
        tableData1: [
          'content     | string | 是    | -        | 是    | 否    | 文本内容',
          'x           | number | 否    | 0        | 是    | 是    | x 坐标，可以为百分比值',
          'y           | number | 否    | 0        | 是    | 是    | y 坐标，可以为百分比值',
          'alpha       | number | 否    | 1        | 是    | 是    | 透明度，取值范围 [0, 1]，0 完全透明, 1 完全不透明',
          'color       | number | 否    | 0xffffff | 是    | 是    | 文本颜色',
          'bold        | number | 否    | 1        | 否    | 否    | 是否加粗',
          'textShadow  | number | 否    | 1        | 否    | 否    | 是否字体阴影',
          'strokeWidth | number | 否    | 0        | 否    | 否    | 描边宽度',
          'strokeColor | number | 否    | 0x000000 | 否    | 否    | 描边颜色',
          'anchorX     | number | 否    | 0        | 否    | 否    | 锚点，位置为长度的百分比',
          'anchorY     | number | 否    | 0        | 否    | 否    | 锚点，位置为宽度的百分比',
          'zIndex      | number | 否    | 0        | 否    | 否    | 层次权重，值越高的对象在上层',
          'rotateX     | number | 否    | 0        | 是    | 是    | X轴旋转',
          'rotateY     | number | 否    | 0        | 是    | 是    | Y轴旋转',
          'rotateZ     | number | 否    | 0        | 是    | 是    | Z轴旋转',
          'parent      | text   | 否    | -        | 否    | 否    | 所属层',
          'fontSize    | number | 否    | 25       | 是    | 否    | 文本字体大小，可以为百分比（在HTML5平台，最终字体大小为当前屏幕宽度*字体百分比px）',
          'fontFamily  | string | 否    | ""       | 否    | 否    | 文本字体，默认值为平台默认字体。如果字体不存在时，使用平台默认字体',
          'scale       | number | 否    | 1        | 是    | 是    | 缩放'
        ],
        tableData2: [
          'text      | string                   | 否    | ""       | 是    | 否    | 按钮标签',
          'fontSize  | number                   | 否    | 25       | 是    | 否    | 按钮字体大小',
          'textColor | number                   | 否    | 0x000000 | 否    | 否    | 按钮文字颜色',
          'textAlpha | number                   | 否    | 1        | 否    | 否    | 按钮字体透明度',
          'fillColor | number                   | 否    | 0xffffff | 否    | 否    | 按钮填充颜色',
          'fillAlpha | number                   | 否    | 1        | 否    | 否    | 按钮填充透明度',
          'duration  | time                     | 否    | 0        | 否    | 否    | 按钮显示时间',
          'target    | av object or seek object | 是    | -        | 否    | 否    | 按钮功能',
          'x         | number                   | 否    | 0        | 是    | 是    | 按钮x坐标',
          'y         | number                   | 否    | 0        | 是    | 是    | 按钮y坐标',
        ],
        tableData3: [
          'av   | number | yes  | -    | -    | -    | 视频av号',
          'page | number | no   | 1    | -    | -    | 视频分P号',
          'time | time   | no   | 0s   | -    | -    | 视频开始播放点'
        ],
        tableData4: [
          'seasonId  | number | yes  | -    | -    | -    | 番剧id',
          'episodeId | number | yes  | -    | -    | -    | 番剧分集id',
          'time      | time   | no   | 0s   | -    | -    | 视频开始播放点'
        ],
        tableData5: [
          'time | time | 是    | -    | -    | -    | seek目标时间'
        ],
        tableData6: [
          'd | string | 是 | "" | 否 | 否 | svg 路径',
          'x | number | 否 | 0 | 是 | 是 | x坐标，可以为百分比值',
          'y | number | 否 | 0 | 是 | 是 | y坐标，可以为百分比值',
          'scale | number | 否 | 1 | 否 | 否 | 缩放',
          'borderWidth | number | 否 | 0 | 否 | 否 | 描边宽度',
          'borderColor | number | 否 | 0 | 否 | 否 | 描边颜色',
          'borderAlpha | number | 否 | 1 | 否 | 否 | 描边透明度',
          'fillColor | number | 否 | 0xffffff | 否 | 否 | 填充颜色',
          'fillAlpha | number | 否 | 1 | 否 | 否 | 填充透明度',
          'viewBox | string | 否 | - | 否 | 否 | svg 画布大小，默认完整显示',
          'width | number | 否 | - | 否 | 否 | 宽度，可以为百分比值，需要设置 viewBox 才可以生效',
          'height | number | 否 | - | 否 | 否 | 宽度，可以为百分比值，需要设置 viewBox 才可以生效'
        ]
      }
    }
  }
</script>


